import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Client from "@/views/Client";
import Address from "@/views/Address";
import Passport from "@/views/Passport";

const routes = [
  {
    path: '/',
    name: 'Client',
    meta: {layout: 'main'},
    component: Client
  },
  {
    path: '',
    name: 'Address',
    meta: {layout: 'main'},
    component: Address
  },
  {
    path: '',
    name: 'Passport',
    meta: {layout: 'main'},
    component: Passport
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
